import React from 'react';
import Particles from 'react-particles-js';
import settings from './settings.json';

export default class ParticleWave extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Particles
        params={settings}
        style={{
          position: 'absolute',
          left: '0',
          top: '0',
          animation: 'wave 1s alternate',
          transform: 'perspective(20rem) rotateX(20deg)'
        }}
      />
    );
  }
}
